@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap);
*,
*::after,
*::before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: "Poppins", sans-serif;
  min-height: 100vh;
}

.container {
  width: 100%;
  max-width: 1440px;
  display: block;
  margin: auto;
  padding: 0 4em;
}

@media only screen and (max-width: 640px) {
  .container {
    padding: 0;
  }
}

.flex-container {
  display: flex;
}

.justify-between {
  justify-content: space-between;
}

a {
  text-decoration: none;
  color: inherit;
}

header {
  background: #2b2b2b;
  text-align: center;
  color: #f7f7f7;
  padding: 0.5em;
}

.cashier {
  padding-top: 5vh;
  display: block;
  width: 80%;
  margin: auto;
}

.search-item {
  margin-bottom: 1vh;
  width: 100%;
  display: flex;
}

.search-item .form-control {
  width: 100%;
  position: relative;
}

.search-item .suggested-item {
  position: absolute;
  bottom: -99px;
  left: 0;
  height: 99px;
  width: min(240px, calc(100% - 80px));
  background-color: #2b2b2b;
  color: #f7f7f7;
  z-index: 10;
  overflow-y: auto;
}

.search-item .suggested-list {
  cursor: pointer;
  padding: 0.25em;
  height: 33px;
  transition: background 0.2s ease-in-out, color 0.2s ease-in-out;
}

.search-item .suggested-list:hover {
  color: #2b2b2b;
  background-color: #f7f7f7;
}

.search-item .form-input {
  width: 100%;
}

.search-item input {
  outline: none;
  border: none;
  padding: 0.5em;
  font-size: 16px;
  box-sizing: border-box;
}

.search-item #item-name {
  width: min(240px, calc(100% - 80px));
  border: 1px solid #2b2b2b;
  border-radius: 0;
}

.search-item #submit-btn {
  cursor: pointer;
  width: 80px;
  background-color: #2b2b2b;
  color: #f7f7f7;
  border: 1px solid #2b2b2b;
}

.cashier-table {
  border: solid 1px #2b2b2b;
  margin-bottom: 10px;
}

.cashier-table-content {
  height: calc(33px * 10);
  overflow-y: auto;
}

.list-header,
.list-total .total-header {
  background-color: #2b2b2b;
  color: #f8f8f8;
}

.list-item:nth-child(2n) {
  background-color: #f7f7f7;
  height: 33px;
}

.list-item:nth-child(2n + 1) {
  background-color: #e4e4e4;
  height: 33px;
}

.list-item,
.list-header,
.list-total {
  display: flex;
}

.list-item *,
.list-header *,
.list-total * {
  padding: 0.25em;
  overflow: hidden;
}

.list-header,
.total-header {
  font-weight: 600;
}

.list-item .qty,
.list-header .qty {
  width: 10%;
}

.list-item .name,
.list-header .name {
  width: 40%;
}

.list-total .total-header {
  width: 75%;
  text-align: center;
  padding: 0.5em 0.25em;
}

.list-total .total-content {
  padding: 0.5em 0.25em;
  border-top: solid 1px #2b2b2b;
}

.list-item .price,
.list-item .subtotal,
.list-header .price,
.list-header .subtotal,
.list-total .total-content {
  width: 25%;
  text-align: right;
}

.btn-bayar {
  width: max(200px, 20%);
  padding: 0.5em;
  text-align: center;
  margin-left: auto;
  margin-right: 0;
  background-color: #2b2b2b;
  color: #f7f7f7;
  margin-bottom: 10px;
  cursor: pointer;
}

.form-udpate-quantity {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 1em;
  background-color: #a3a3a3;
  padding: 2em;
}

.form-udpate-quantity h4 {
  margin-bottom: 5px;
}

.form-udpate-quantity input {
  display: block;
  padding: 0.5em;
  outline: none;
  border: none;
  background: transparent;
}

.form-udpate-quantity input[type="number"] {
  border-bottom: solid 1px #2b2b2b;
  margin-bottom: 0.25em;
}

.form-udpate-quantity input[type="submit"] {
  background-color: #2b2b2b;
  color: #f7f7f7;
  border-radius: 0.25em;
  margin: auto;
}

@media only screen and (max-width: 600px) {
  .cashier {
    width: 90%;
  }

  .cashier-table .qty {
    width: 5%;
  }

  .cashier-table .subtotal {
    width: 30%;
  }
}

footer {
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 0.4em 0;
  background: #2b2b2b;
  color: #f7f7f7;
}

footer .footer-item {
  padding: 0.5em;
}

.footer-item a.link {
  margin-left: 2em;
}

